.rightarrow {
  position: absolute;
  right: 10px;
  top: 43%;
  transition: revert;
  transform: rotate(180deg);
  cursor: pointer;
}

.leftarrow {
  position: absolute;
  left: 10px;
  top: 43%;
  cursor: pointer;
}

.rightarrow.disable-arrow,
.leftarrow.disable-arrow {
  filter: grayscale(100%);
}

.trail-text {
