/* responsive changes start */
.resrange,
.resunits {
  display: none;
}

@media screen and (max-width: 767px) {

  .resrange,
  .resunits {
    display: block;
  }

  .resheader span {
    color: #beb9b9;
  }


  .resheader {
    display: flex;
    justify-content: center;
  }

  .mat-column-Range,
  .mat-column-Units {
    display: none;
  }

  #result-block {
    min-width: 170px;
    max-width: inherit;
    width: inherit;
  }

  .restest {
    justify-content: center;
    text-align: center;
  }

  .reshd {
    text-align: center !important;
  }

  td.mat-cell:first-of-type,
  td.mat-footer-cell:first-of-type,
  th.mat-header-cell:first-of-type {
    padding-left: 32px !important;
    min-width: 150px;
  }

  .pink {
    justify-content: center;
  }

  table th.mat-header-cell {
    min-width: 175px;
  }

}

/* responsive changes end */
